import { getNowDateTime } from "@/utils/index";
import { LimitType } from "@/utils/enum";
import { FuncType, sendToAPP } from "@/utils/interFlow";

let that = null;
let gatewayConn = null;
let startServer = false; // 
const gatewaySignalR = {
  router: null,
  reConnet: false,
  heartTimeStamp: 0,
  init (obj) {
    that = obj;
  },
  openGatewaySocket () {
    if (!that.$store.getters.gatewayIP) return;
    if (!that.$route.meta || !that.$route.meta.limits || that.$route.meta.limits.indexOf(LimitType.inCourse) === -1) {
      return;
    }
    gatewaySignalR.reConnet = true;
    // eslint-disable-next-line no-undef
    gatewayConn = $.hubConnection("http://" + that.$store.getters.gatewayIP + ":" + that.$config.gatewaySignalRPort);
    window.GatewayHub = gatewayConn.createHubProxy("TerminalHub");

    // Hubs OnConnected 
    gatewaySignalR.signalRFunction();

    gatewayConn.start().done(data => {
      console.log(getNowDateTime(), "gatewaySignal");
      gatewaySignalR.signalRGroup();
    }).fail(err => {
      console.log(getNowDateTime(), "gatewaySignal", err);
    });

    gatewayConn.disconnected(() => {
      console.log(getNowDateTime(), "gatewaySignal", startServer);
    });

    // stateChanged    
    gatewayConn.stateChanged(data => {
      // console.log("data:", data);
      // console.log("", data.newState, this.reConnet);
      if (data.newState !== 1 && startServer) {
        gatewaySignalR.startGatewaySignalR();
      } else {
        gatewaySignalR.reConnet = false;
        if (data.newState === 1) {
          // gatewaySignalR.getScreenInfo();
          gatewaySignalR.validateCourse();
        }
        // 
        gatewaySignalR.heartTimeStamp = 0;
        gatewaySignalR.startHeart();
      }
    });
  },
  /**
   * signalR
   */
  signalRGroup () {
    window.GatewayHub.invoke("QuitGroup", "All").then(res => {
      console.log("ALL");
    }).catch(err => {
      console.log("err", err);
    });

    setTimeout(() => {
      window.GatewayHub.invoke("JoinGroup", "All").then(res => {
        console.log("ALL");
      }).catch(err => {
        console.log("err", err);
      });
    }, 500);

    window.GatewayHub.invoke("QuitGroup", that.$store.getters.teacherID).then(res => {
      console.log(":", that.$store.getters.teacherID);
    }).catch(err => {
      console.log("err", err);
    });
    setTimeout(() => {
      window.GatewayHub.invoke("JoinGroup", that.$store.getters.teacherID).then(res => {
        console.log("", that.$store.getters.teacherID);
      }).catch(err => {
        console.log("err", err);
      });
    }, 300);
  },
  /**
   * signalR
   */
  signalRFunction () {
    // 
    window.GatewayHub.on("ReciveStartClass", courseId => {
      // console.log("ReciveStartClass:", courseId);
      ReciveStartClass({ state: 1 });
    });
    window.GatewayHub.on("ReciveOverClass", value => {
      ReciveStartClass({ state: 0 });
    });

    // 
    window.GatewayHub.on("ReciveIsAllowThrowScreen", value => {
      reciveThrowScreenState({ state: value });
    });
  },

  /**
   * signalR
   */
  startGatewaySignalR () {
    if (!gatewaySignalR.reConnet) {
      startServer = true;
      console.log(getNowDateTime(), "gatewaySignalR");
      gatewaySignalR.reConnetServer();
    }
  },
  /**
   * signalR
   */
  reConnetServer () {
    // 
    if (!that.$route.meta || !that.$route.meta.limits || that.$route.meta.limits.indexOf(LimitType.inCourse) === -1) {
      return;
    }
    if (!gatewayConn || gatewayConn.state !== 1) {
      // console.log(this.getNowDateTime(), "signalR");
      gatewaySignalR.openGatewaySocket();
      setTimeout(() => {
        gatewaySignalR.reConnetServer();
      }, 2000);
    }
  },
  /**
   * signalR
   */
  stopServer () {
    startServer = false;
    gatewaySignalR.reConnet = false;
    console.log(getNowDateTime(), "");
    if (gatewayConn) {
      gatewayConn.stop(false, true);
    }
    if (that && that.timerSignalR) {
      window.clearInterval(that.timerSignalR);
    }
  },
  startRegister () {
    let IP = that.$store.getters.localIP;
    sendToAPP(FuncType.initLocalIP, "").then(res => {
      if (res) that.$store.commit("SET_IP", res);
    });

    let reqData = {
      studentID: that.$store.getters.studentID,
      studentIP: that.$store.getters.localIP || IP
    };
    that.$gatewayHttp.student.getRegister(reqData).then(res => {
      if (res.Result) {
        console.log(getNowDateTime(), "IP");
      }
    }).catch(() => {
    });
  },
  startHeart () {
    let timeStamp = new Date().getTime();
    // 
    if (timeStamp - gatewaySignalR.heartTimeStamp > 2000) {
      gatewaySignalR.heartTimeStamp = timeStamp;
      if (gatewayConn && gatewayConn.state === 1) {
        let message = {
          Type: 1,
          ID: that.$store.getters.teacherID || "",
          IP: that.$store.getters.localIP || ""
        };
        window.GatewayHub.invoke("Heart", JSON.stringify(message)).then(res => {
          console.log("Heart-res:", res);
          setTimeout(() => {
            gatewaySignalR.startHeart();
          }, that.$config.SignalRHeartTime || 2000);
        }).catch(err => {
          console.log("Heart-err", err);
        });
      } else {
        // 
        if (!that.$route.meta || !that.$route.meta.limits || that.$route.meta.limits.indexOf(LimitType.inCourse) === -1) {
          return;
        }
        setTimeout(() => {
          gatewaySignalR.startHeart();
        }, that.$config.SignalRHeartTime || 2000);
      }
    }
  },
  validateCourse () {
    that.$store.dispatch('ValidateCourse').then(res => {
      if (res) {
        this.exitCourse();
      }
    }).catch(() => {
    });
  },
  exitCourse () {
    that.$notify.error({
      title: '',
      message: ''
    });

    setTimeout(() => {
      turnPath("/subject/view");
    }, 1800);
  },
  getScreenInfo () {
    that.$gatewayHttp.student.getScreenInfo().then(res => {
      if (res.Result) {
        delete res.Result;
        let info = JSON.parse(JSON.stringify(res));
        console.log(info);
        sendToAPP(FuncType.screenSetting, JSON.stringify(info)).then(res => {
          gatewaySignalR.startRegister();
        }).catch(() => {
          gatewaySignalR.startRegister();
        });
      }
    }).catch(() => {
      gatewaySignalR.startRegister();
    });
  }
};

/**
 * 
 */
function ReciveStartClass (value) {
  if (value.state === 0) {
    that.$store.commit("QUIT_COURSE");
    turnPath("/subject/view");
  }
  // 
  // if (that.$route.path.indexOf("/home-course") > -1) {
  // } else {
  //   that.$router.push("/home-course");
  // }
}

/**
 * 
 * @param toPath 
 */
function turnPath (toPath) {
  sendToAPP(FuncType.toPath, toPath);
  if (that.$route.path.indexOf(toPath) === -1) {
    that.$router.replace(toPath);
  }
}

/**
 * 
 * @param {*} value
 */
function reciveThrowScreenState (value) {
  sendToAPP(FuncType.searchScreenState, value.state ? "1" : "0");
}

document.addEventListener("visibilitychange", function () {
  if (!document.hidden) {
    console.log(getNowDateTime(), "");
    if (that.$route.meta && that.$route.meta.limits && that.$route.meta.limits.indexOf(LimitType.inCourse) > -1) {
      gatewaySignalR.startGatewaySignalR();
      // 
      if (that.$route.meta.limits.indexOf(LimitType.group) > -1) {
        that.$store.commit("SET_GROUPID", that.$store.getters.groupID);
      }
    }
  }
});
export default gatewaySignalR;
